/* Define a fade-in animation */
.animate-fade-in {
    opacity: 0;
    transform: translateY(20px);
    animation: fade-in 1s ease forwards;
  }
  
  @keyframes fade-in {
    from {
      opacity: 0;
      transform: translateY(20px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  